#navbar-container {
  background-color: #0d6efd;
  letter-spacing: 3px;
  font-family: "Architects Daughter", cursive;
  font-weight: bold;
  text-transform: uppercase;
}

.nav-link {
  color: #fff !important;
}

.navbar-brand {
  margin-right: auto !important;
}

.navbar-toggler {
  background-color: #00c853 !important;
}

.landing-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  height: 100vh;
  justify-content: center;
  background: linear-gradient(rgba(13, 110, 253, 0.9), rgba(13, 110, 253, 0.9)), url("./../images/geometric.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.landing-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  font-family: "Architects Daughter", cursive;
  color: #fff;
}

.landing-title {
  font-weight: bold;
  font-size: 3.8rem;
  text-transform: uppercase;
  text-shadow: -0.01em 0.01em 0.1em black;
}

.landing-subtitle-wrapper {
  display: flex;
  flex-direction: column;
}

.landing-subtitle {
  color: #eee;
  font-size: 1.5rem;
  text-transform: lowercase;
}

@media screen and (min-width: 1000px) {
  .landing-title {
    font-size: 4.2rem;
  }

  .landing-subtitle {
    font-size: 2rem;
  }
}
.contact-info-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-color: #00c853;
  padding: 85px 20px;
}

.contact-title-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Viga", sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  color: #fff;
  text-shadow: -0.01em 0.01em 0.1em black;
  padding-bottom: 40px;
}

.contact-title {
  font-size: 40px;
}

.contact-title-subtext {
  font-size: 16px;
}

.profile-pic-wrapper {
  padding-bottom: 40px;
}

.profile-pic {
  border-radius: 50%;
  max-height: 150px;
  max-width: 100%;
}

.info-icons {
  display: flex;
  flex-direction: row;
  width: 200px;
  justify-content: space-between;
}

.info-icon {
  border-radius: 50%;
  max-height: 30px;
  width: 30px;
}

.about-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.about-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 5vh 10vw;
  font-family: "Fira Mono", monospace;
  text-align: center;
  border-bottom: 2px solid #0d6efd;
  background: linear-gradient(rgba(0, 0, 0, 0.98), rgba(0, 0, 0, 0.98)), url("./../images/geometric.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.about-title {
  margin-bottom: 5vh;
  font-size: 3rem;
  font-family: "Cuprum", sans-serif;
  text-transform: uppercase;
  letter-spacing: 4px;
}

.about-img-container {
  height: auto;
  margin-bottom: 5vh;
}

.about-img {
  width: 100%;
  max-width: 400px;
  border-radius: 20px;
}

.about-info-text {
  text-align: center;
}

.about-info-text {
  opacity: 0.9;
  font-size: 0.9rem;
}

@media screen and (min-width: 700px) {
  .about-content-wrapper {
    width: 100%;
  }

  .about-image-container {
    width: 30vw;
    float: left;
    margin: 5vh 5vw;
  }

  .about-info-text {
    font-size: 1.25rem;
  }
}
@media screen and (min-width: 1000px) {
  .about-title {
    margin: 5vh;
    font-size: 5rem;
    letter-spacing: 6px;
  }

  .about-content-wrapper {
    max-width: 900px;
    letter-spacing: 4px;
  }

  .about-image-container {
    max-width: 350px;
    margin-left: -5%;
  }

  .about-info-text {
    font-size: 1.5rem;
    line-height: 1.8em;
    margin-bottom: 40px;
  }
}
.timeline {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 85px 20px;
  border-bottom: 1px solid #0d6efd;
  background-color: black;
  font-family: "Cuprum", sans-serif;
}

.timeline-header {
  padding-bottom: 40px;
  color: white;
  font-family: "Fira Mono", monospace;
}

.timeline-title {
  font-size: 3rem;
}

.timeline-subtitle {
  display: none;
}

.timeline-items-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
}

.timeline-item-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin: 10px;
  padding: 5px 10px;
  height: 200px;
  max-width: 400px;
  border-radius: 10%;
}

.timeline-item-wrapper:nth-child(even) {
  flex-direction: row-reverse;
}

.timeline-image-container {
  width: 30vw;
}

.timeline-image {
  width: 100%;
  border-radius: 15%;
}

.tile {
  width: 55vw;
}

.timeline-item-wrapper:nth-child(1) {
  background-color: #24a5dc;
}

.timeline-item-wrapper:nth-child(2) {
  background-color: #df802e;
}

.timeline-item-wrapper:nth-child(3) {
  background-color: #25d13c;
}

.timeline-item-wrapper:nth-child(4) {
  background-color: #f04419;
}

.timeline-item-wrapper:nth-child(5) {
  background-color: #a624e7;
}

.timeline-item-wrapper:nth-child(6) {
  background-color: #d6d019;
}

.timeline-item-wrapper:nth-child(7) {
  background-color: #0d6efd;
}

.final-image-wrapper {
  margin: 40px;
  display: none;
}

.tile-grow {
  transition: all 0.4s ease-in-out;
}

.tile-grow:hover {
  transform: scale(1.2);
}

@media screen and (min-width: 700px) {
  .timeline-item-wrapper:nth-child(odd) {
    margin-right: 35%;
  }
  .timeline-item-wrapper:nth-child(odd) .timeline-image-container {
    height: 120px;
    transform: translateX(400%);
  }
  .timeline-item-wrapper:nth-child(odd) .timeline-image {
    border-radius: 50%;
    min-width: 120px;
    height: 100%;
    object-fit: cover;
    object-position: center center;
  }
  .timeline-item-wrapper:nth-child(odd) .tile {
    margin-right: 5vw;
  }
  .timeline-item-wrapper:nth-child(odd) .tile-body {
    width: 300px;
    padding-right: 25%;
    align-self: center;
  }
  .timeline-item-wrapper:nth-child(odd) .tile-title {
    font-size: 2.5rem;
  }

  .timeline-item-wrapper:nth-child(even) {
    margin-left: 25%;
  }
  .timeline-item-wrapper:nth-child(even) .timeline-image-container {
    height: 120px;
    transform: translateX(-400%);
  }
  .timeline-item-wrapper:nth-child(even) .timeline-image {
    border-radius: 50%;
    min-width: 120px;
    height: 100%;
    object-fit: cover;
    object-position: center center;
  }
  .timeline-item-wrapper:nth-child(even) .tile-body {
    width: 375px;
    margin-left: 16.5%;
    align-self: center;
  }
  .timeline-item-wrapper:nth-child(even) .tile-title {
    font-size: 2.5rem;
  }
}
@media screen and (min-width: 700px) and (min-width: 900px) {
  .timeline-item-wrapper {
    margin-bottom: 5vh;
  }
}
@media screen and (min-width: 700px) and (min-width: 1050px) {
  .timeline-item-wrapper.tile-title {
    font-size: 3rem;
  }

  .tile-detail {
    font-size: 1.25rem;
  }
}
.tech-stack {
  background-color: #00c853;
  border-bottom: 2px solid #0d6efd;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 5vh 5vw;
  font-family: "Cuprum", sans-serif;
}

.tech-stack-title {
  font-size: 3rem;
  margin-bottom: 5vh;
  text-shadow: -0.01em 0.01em 0.1em black;
  text-transform: uppercase;
}

.tech-stack-subtitle {
  display: none;
}

.icon-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.icon-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 5vh 0;
}

.icon-title {
  opacity: 0.8;
  margin-bottom: 10px;
  font-family: "Amatic SC", cursive;
  font-size: 2.5rem;
  text-transform: uppercase;
  text-shadow: -0.01em 0.01em 0.1em black;
  letter-spacing: 4px;
}

.icon-img-container {
  width: 100px;
  height: 100px;
}

.icon-img {
  width: 100%;
  height: 100%;
}

@media screen and (min-width: 600px) {
  .icon-wrapper {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
  }

  .icon-container {
    width: 40vw;
  }
}
@media screen and (min-width: 850px) {
  .icon-wrapper {
    width: 100%;
    max-width: 1030px;
  }

  .icon-container {
    width: fit-content;
    flex-wrap: nowrap;
  }
}
.blog-page-wrapper {
  display: flex;
  justify-content: center;
  color: #00c853;
  min-height: 100vh;
  background: linear-gradient(rgba(0, 0, 0, 0.98), rgba(0, 0, 0, 0.98)), url("./../images/geometric.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.blog-main-title-wrapper {
  font-family: "Architects Daughter", cursive;
  font-weight: bold;
  color: #0d6efd;
  text-align: center;
  text-transform: uppercase;
}

.blog-main-page {
  display: flex;
  flex-direction: column;
  max-width: 1260px;
  padding: 85px 40px;
}

.blog-articles-info-wrapper {
  display: flex;
  flex-direction: column;
}

.blog-article-box-wrapper {
  display: flex;
  margin-bottom: 30px;
  padding: 20px 15px;
  font-family: "Cuprum", sans-serif;
  border-bottom: 1px solid #0d6efd;
}

.blog-article-box-wrapper:hover {
  box-shadow: 0 0 11px #ffad05;
}

.blog-article-image-wrapper {
  width: 250px;
  height: 200px;
  margin-right: 30px;
}

.blog-article-image {
  width: 100%;
  height: 100%;
}

.blog-article-title {
  font-family: "Cuprum", sans-serif;
  text-transform: uppercase;
  font-size: 1.5rem;
}

.blog-article-description {
  font-size: 1.15rem;
}

.blog-info-wrapper {
  display: flex;
  flex-direction: column;
  flex: 1 1;
}

.blog-articles-wrapper {
  display: flex;
}

.side-panel {
  margin-left: 40px;
  width: 200px;
  height: calc(100% - 30px);
  background: linear-gradient(rgba(13, 110, 253, 0.9), rgba(13, 110, 253, 0.9)), url("./../images/geometric.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}